/* Color Palette */
body {
  background-color: #EDDEA4;
}

/* General Styling */
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

/* Navbar Styling */
.nav-bar {
  width: 100%;
  height: 100px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}

.navigation {
  display: flex;
  margin-top: 10px;
  margin-right: 10px;
}

@media screen and (max-width: 600px) {
  .navigation {
    flex-direction: column;
  }
}

/* Title Styling */
header,
.title,
.subheader,
.subheaderContent,
.subheaderFootnote,
.subheaderSection {
  text-align: center;
  font-family: "Courier New", "Lucida Console", monospace;
}

header {
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 40px;
}

header:hover {
  animation: calm 2s infinite;
}

@keyframes calm {
  0% { background-color: #F5F5DC; }
  20% { background-color: #F0F8FF; }
  40% { background-color: #ADD8E6; }
  60% { background-color: #B0E0E6; }
  80% { background-color: #F5DEB3; }
  100% { background-color: #FFE4C4; }
}

.title {
  margin-top: 20px;
  margin-right: 30px;
  font-size: 30px;
}

.subheader {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 30px;
}

.subheaderContent,
.subheaderFootnote,
.subheaderSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
  margin-left: 4%;
  margin-right: 4%;
}

.subheaderFootnote {
  font-size: 14px;
}

.subheaderSection {
  font-family: "Courier New", "Lucida Console", monospace;
  font-weight: 550;
  margin-top: 80px;
  font-size: 20px;
  margin-bottom: -1%;
}

/* Button Styling */
.roundB,
.contactB {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 300;
  background-color: #0FA3B1;
  border-radius: 8px;
  border: none;
  color: #ecf3ea;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
}

.roundB:hover {
  background-color: #0b7e89;
}

.roundB:active {
  background-color: #073e43;
}

.contactB {
  background-color: #8e5611;
}
.contactB:hover {
  background-color: #6a4309;
}

.contactB:active {
  background-color: #3c2502;
}


.active {
  background-color: #585858;
}

.active:hover,
.active:active {
  background-color: #585858;
}

/* Container Styling */
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.containerResume {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Divider Styling */
.nav-divider {
  margin-bottom: 20px;
  width: 100%;
  height: 10px;
  border-bottom: 1px solid rgba(0, 0, 0);
}

/* Burrito Styling */
.mapContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.row {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-spacing: 10px;
}

.column {
  display: table-cell;
}

/* Animation Styling */
.waviy {
  text-align: center;
  margin-top: 10%;
  position: relative;
  font-size: 60px;
}

.waviy span {
  position: relative;
  display: inline-block;
  margin-right: 100px;
  transition: transform 0.3s ease-in-out;
}

.waviy span:hover {
  transform: scale(1.2);
}



/* PDF Display Styling */
.pdfDisplay {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 90vh;
}

ul,
p {
  font-size: 18px;
  margin-left: 4%;
  margin-right: 4%;
  font-family: "Courier New", "Lucida Console", monospace;
}

@media screen and (max-width: 600px) {
  ul,
  p {
    font-size: 16px;
  }
}
